.video-responsive {

    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    height: 0;
  }
  
  .video-responsive iframe {

    left: 14%;
    top: 0;
    height: 70%;
    width: 70%;
    position: absolute;
  }

  @media (max-width: 700px) {
    .video-responsive iframe {
      left: 5%;
      height: 90%;
      width: 90%;
    }
}