.mobileContainer{
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    overflow: hidden;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    z-index: 12;
    position: fixed;
    right: 100%;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    background-color: #1b1b2f;
    border: 5px solid #e43f5a;

}


.mobileContainer-enter {
    border: none;
    left: 100%;
    /* width: 0%; */

  }
  .mobileContainer-enter-done{
    transition: all 1s;
    left: 0;

    
  }
  .mobileContainer-exit {
    -webkit-align-self: flex-start;
            align-self: flex-start;
    right: 0;
    border: 5px solid #e43f5a;
    
  }
  .mobileContainer-exit-done {
    transition: all 1s;
    right: 100%;


  }
  


