.handbookMenu > ul{
    color: #e43f5a;
    font-family: 'Oswald', sans-serif;
    font-size: 1.5em;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100%;
    height: 90%;
}

.handBooklinkMenu{
    font-size: 1.5em;
    text-decoration: none;
    color: #e43f5a;
}

.handbookMenu-enter {
    border: none;
    left: 100%;
    /* width: 0%; */

  }
  .handbookMenu-enter-done{
    transition: all 1s;
    left: 0;

    
  }
  .handbookMenu-exit {
    -webkit-align-self: flex-start;
            align-self: flex-start;
    right: 0;
    border: 5px solid #e43f5a;
    
  }
  .handbookMenu-exit-done {
    transition: all 1s;
    right: 100%;


  }


  .handBooklink{
    font-size: 1.5em;
    text-decoration: none;
    color: silver;
  }
  .handBookselected{
    text-decoration: underline;
  }

@media (max-width: 900px) {

    .themes{
        display: none;
    }

    table{
        width: 100%;
    }
    .content{
        width: 90%;
    }
    .handBook{
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        -webkit-flex-flow: column;
                flex-flow: column;
    }
    .handbookContent{
        height: auto;
        width: 100%;
    }
    .handbookMenuIcon{
        display: -webkit-flex;
        display: flex;
    }
}
